* {
  font-size: 100%;
}

@media (max-width: 767px) {
  * {
    font-size: 80%;
  }
}

@media (max-width: 500px) {
  * {
    font-size: 60%;
  }
}

@font-face {
  font-family: "MyFont";
  src: local("MyFont"),
    url(../assets/font-family/BasierCircle-Regular.woff) format("woff");
}

h1 {
  font-family: "MyFont";
  font-style: normal;
  font-weight: 700;
  font-size: 2.3rem;
  color: #000000;
}

h2 {
  font-family: "MyFont";
  font-style: normal;
  font-weight: 700;
  font-size: 1.7rem;
  line-height: 34px;
  color: #ffff;
  padding: 10px;
}

h3 {
  font-family: "MyFont";
  font-style: normal;
  font-weight: 600;
  font-size: 1.5rem;
  line-height: 46px;
  color: #000;
  padding: 10px;
}

h4 {
  font-family: "MyFont";
  font-style: normal;
  font-weight: 600 !important;
  font-size: 1.25rem;
  line-height: 24px !important;
  color: #000000;
  padding: 10px;
}

p {
  font-family: "MyFont";
  font-style: normal;
  font-weight: 400;
  font-size: 1rem;
  line-height: 24px;
  color: #818181;
}

.navbarPage {
  box-shadow: 0px 3px 10px 0px rgba(0, 0, 0, 0.1);
  background-color: #ffffff;
}

.landing-container {
  padding: 0px;
  /* background-color: #f3f5f7; */
}

.landing-row {
  /* padding: 80px; */
