/* testimonials css.................................. */

/* Testimonials Container */
.testimonials-container {
  padding: 3.1rem 0;
}

/* Testimonials Heading */
.testimonials-heading {
  font-size: 1.75rem;
  font-weight: bold;
  text-decoration: underline;
}

/* Individual Testimonial */
.testimonial {
  padding: 1.25rem;
}

/* Avatar Image */
.avatar {
  width: 12.5rem;
  height: 12.5rem;
}

/* Testimonial Quote */
.quote {
  font-size: 1.25rem;
  font-style: italic;
  margin-bottom: 10px;
}

/* Testimonial Author */
.author {
  font-size: 1.1rem;
  font-weight: bold;
  margin-bottom: 5px;
}

/* Additional Paragraph */
.additional-paragraph {
  font-size: 1rem;
}

