/* Our services.............................. */

.services h3 {
  position: relative;
  font-weight: bold;
  text-transform: uppercase;
  padding: 1.25rem;
  text-align: center;
}

.service-row .box {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background-color: #ffffff;
  padding: 1.87rem 1.5rem;
  -webkit-box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.2);
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.2);
  margin-top: 3rem;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.service-row .box img {
  width: 4.6rem;
}

.service-row .detail-box {
  margin-left: 1.5rem;
}

.service-row .detail-box h5 {
  font-size: 1.1rem;
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 20px;
}

.service-row .detail-box .read-btn {
  display: inline-block;
  padding: 8px 35px;
  background-color: #ce202f;
  color: #ffffff;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  border: 1px solid transparent;
  margin-top: 10px;
}

/* Contact us........................ */
.background-contact-img {
  background-image: url("https://html.design/demo/viseas/images/contact-bg.jpg");
  background-size: cover;
  background-position: center;
  height: 80vh;
  width: 100vw;
}

.text-container-contact {
  position: relative;
  color: white; /* Text color */
  padding: 50px; /* Adjust padding as needed */
  background: linear-gradient(
    to right,
    rgba(0, 0, 0, 0.7),
    rgba(0, 0, 0, 0.25)
  );
  height: 80vh;
}

.btn .main-btn {
  margin-top: 10px;
  border: none;
  text-transform: uppercase;
  display: inline-block;
  padding: 1rem 3.1rem;
  background-color: #ce202f;
  color: #ffffff;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  border: 1px solid transparent;
  font-weight: bold;
}

